.button {
    height: 30px;
    padding:5px 24px 5px 24px;
    border-radius: 3px;
    border: none;
    margin-left: 132px;

}

.disabled {
    background-color: #004D99;
    opacity: 0.5;
    color:#002e5c;
    cursor: not-allowed;
}

.secondary {
    background-color:#fff;
    color:#0066CC;
    border: 1px solid  #0066CC;
}
.default {
    background-color: #0066CC;
    color:#fff;
}
.red {
    background-color:#CC1439;
    color:#fff;
}
.default:hover {
    background-color: #0080FF;
}
.red:hover {
    background-color: #FF1A47;
}
.secondary:hover {
    background-color: #E5F0FA;
    border: 1px solid  #0066CC;

}

.default:active {
    background-color: #0059B2;
}
.secondary:active {
    background-color:#C9E5FF;
}
.red:active {
    background-color: #B21232;
}
.button:disabled {
    background-color: #004D99;
    opacity: 70%;

}
